import { getConfig } from '../scripts/globals/config';
import 'lazysizes';
import 'lazysizes/plugins/parent-fit/ls.parent-fit';

const createRestaurantDetail = (restaurant) => {
  const { apiUrl } = getConfig();

  const imageUrl = `${apiUrl}/large/${restaurant.pictureId}`;

  return `
  <div class="detailFlexContainer">
  <img src="${imageUrl}" alt="${restaurant.name}" class="detailBackground">
  <div class="detailDescription">
  <h2>${restaurant.name}</h2>
  <p class="restDesc">${restaurant.description}</p><br><br>
  <p class="kota">City : ${restaurant.city}</p>
  <p><span style='font-weight: bold;'>Address :</span><br> ${restaurant.address}</p>
  <p><span style='font-weight: bold;'>Categories :</span><br></p>
  <ul style="text-align: center;">
    ${restaurant?.categories.map((name) => `
        <p>${name.name}</p>
      `).join('')}
    </ul>
  <p><span style='font-weight: bold;'>Rating :</span><br> ‚≠ê${restaurant.rating}</p>
  </div>
  </div>

    <h3 class="menuBar">ü•ûMenus‚òï</h3>

    <div class="menus">
    <div class="flexMenusItem">
    <h4>Food</h4><br>
    <ul>
    ${restaurant?.menus.foods.map((food) => `
        <li>${food.name}</li>
      `).join('')}
    </ul>
    </div>

    <div class="flexMenusItem">    
    <h4>Drink</h4><br>
    <ul>
    ${restaurant?.menus.drinks.map((drink) => `
        <li>${drink.name}</li>
      `).join('')}
  </ul>
  </div>
  </div>

  <div>
    <h3 class="customerHeading">‚≠ê‚≠ê‚≠êCustomer Reviews‚≠ê‚≠ê‚≠ê</h3>
    <ul>
    ${restaurant?.customerReviews.map((review) => `
    <li style="list-style-type: none">
      <div class="review-item">
        <p>${review.name}</p>
        <p>${review.review}</p>
        <p>${review.date}</p>
      </div>
    </li>
  `).join('')}
    </ul>

    <button id="reviewButton" class="addButton">Add Review</button>

    <div id="reviewForm" class="formReview">
        <form id="reviewForm">
        <label>Name : </label>
        <input type="text" id="reviewName" placeholder="" required><br>
        <label>Review :</label>
        <textarea id="reviewContent" placeholder="" required></textarea><br>
        <button type="submit" class="submitReviewButton">Submit</button>
        </form>
      </div>
    </div>

  </div>
`;
};

const createRestaurantTemplate = (restaurant) => {
  const { apiUrl, imageResolutions } = getConfig();
  let resolution = 'medium';
  const screenWidth = window.innerWidth;

  if (screenWidth <= 768) {
    resolution = 'small';
  } else if (screenWidth > 768 && screenWidth <= 1024) {
    resolution = 'medium';
  } else {
    resolution = 'large';
  }

  const imageUrl = `${apiUrl}/${imageResolutions[resolution]}/${restaurant.pictureId}`;

  return `
<div class="restaurant">
<img class="lazyload rest-img" data-src="${imageUrl}" alt="${restaurant.name}">
<p class="kota">City : ${restaurant.city}</p>
<div>
<a href="#/detail/${restaurant.id}" class="detail_a">
<h2>${restaurant.name}</h2>
</a>
<p>Rating : ‚≠ê${restaurant.rating}</p>
</div>
`;
};

const createLikeRestaurantTemplate = () => `
  <button aria-label="like this restaurant" id="likeButton" class="like">
     <i class="fa fa-heart-o" aria-hidden="true"></i>
  </button>
`;

const createUnlikeRestaurantButtonTemplate = () => `
  <button aria-label="unlike this restaurant" id="likeButton" class="like">
    <i class="fa fa-heart" aria-hidden="true"></i>
  </button>
`;

export {
  createRestaurantDetail,
  createRestaurantTemplate,
  createLikeRestaurantTemplate,
  createUnlikeRestaurantButtonTemplate,
};
