___CSS_LOADER_EXPORT___.push([module.id, `* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.detail_a h2{
  min-width: 45px;
  min-height: 45px;
}

body {
  font-family: "Montserrat", sans-serif;
  overflow-x: hidden;
  background-color: #FFFBE9;
}

img {
  display: block;
}

.detail_a {
  text-decoration: none;
  color: black;
}

.detail_a :hover {
  font-weight: bold;
  color: #617A55;
}

.nav-bar {
  display: flex;
  width: 100%;
  color: black;
  font-size: large;
  justify-content: space-around;
  padding: 20px 0;
  background-color: #CCD6A6;
}


.nav-bar ul {
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  width: 40%;
  align-items: center;
}

.nav-bar ul li a {
  text-decoration: none;
  color: black;
  padding: 2em;
}

.nav-bar ul li a:hover {
  font-weight: bold;
}

.logo {
  display: flex;
  align-items: center;
  gap: 1em;
}

.logo-img {
  width: 150px;
  display: flex;
}

.logo h1 {
    font-family: "Yellowtail", cursive;
    font-weight: bold;
    font-size: 2em;
}

.togle {
  display: none;
  color: black;
  border: none;
  align-items: center;
  background-color: #CCD6A6 ;
  font-size: larger;
  min-width: 44px;
  min-height: 44px;
}

.gbrHero {
  width: 100%;
  height: 100vh;
}

.herotxt {
  position: absolute;
  bottom: 20vh;
  background-color: transparent;
  left: 50%; 
  transform: translateX(-50%); 
  text-align: center;
  color: white;
  padding: 5em 8em;
  width: 100%;
}

.herotxt h2 {
  font-size: 4em;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  max-width: 100%;
  margin: 2em;
  gap: 1em;
}

.restaurant {
  position: relative;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #F4EAD5;
}

.rest-img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 28em;
  height: 20em; 
  padding: 1.5em;
  border-radius: 2em;
}

.restaurant h2 {
  text-align: center;
}

.restaurant p {
  margin-top: 1.5em;
  margin-bottom: 1em;
  text-align: center;
  padding: 0em 2em;
}

.expl {
  text-align: center;
  padding: 1em;
  padding-top: 5em;
}

footer {
  max-width: 100%;
  text-align: center;
  padding: 3em;
}

.moreBtn {
  color: blue;
  background: none;
  border: none;
  display: block;
  margin: 0 auto;
  min-width: 44px;
  min-height: 44px;
}

.moreBtn:hover {
  font-weight: bold;
  cursor: pointer;
}

.restDesc {
  max-height: 5em;
  overflow: hidden;
}

.restDescLong {
  max-height: none;
  overflow: visible;
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  padding: 8px;
  background-color: #617A55;
  color: white;
  z-index: 100;
}

.skip-link:focus {
  top: 0;
}

.foo {
  background-color: #DAE2B6;
}

.kota {
  left: 0;
  display: block;
  position: absolute;
  width: 15em;
  top: 1em;
  background-color: #ffc293;
  padding: 1em !important;
}

.detailFlexContainer {
  display: flex;
}
.detailDescription {
  padding: 7em 5em 5em 5em;
}
.detailBackground {
  position: relative;
  max-width: 50%;
}

.menuBar {
  background-color: #617A55;
  color: white;
  margin-top: 1em;
  padding: 2em;
  text-align: center;
font-size: 1.5em;}

.menus {
  display: flex;
  justify-content: center;
  gap: 5em;
  padding: 3em 2em;
}

.flexMenusItem {
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 2em 3em;
  border: 2px solid black;
  font-size: large;
  border-radius: 15px;
  width: 30em;
  background-color: #fdd4b5;
}

.menus ul li {
  list-style: none;
}

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;
  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 18px;
  color: white;
  cursor: pointer;
}

.customerHeading {
  background-color: #617A55;
  color: white;
  text-align: center;
  padding: 2em;
}

.review-item {
  text-decoration: none;
  list-style: none;
  border: 2px solid black;
  margin: 2em ;
  border-radius: 10px;
  background-color: white;
}

/* Favorite */
.headingFavorite {
  text-align: center;
  padding: 1.5em;
}

.formReview {
  display: none;
  background-color: white;
  padding: 2em;
  margin: 3em 0;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  width: 50%;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  z-index: 1000;
}

.formReview input {
  padding: 1em;
  border: 2px solid #ccc;
  border-radius: 5px;
  width: 250px;
  margin-left: 15px;
}

.formReview textarea {
  margin-bottom: 10px;
  margin-left: 10px;
  padding: 10px;
  margin-top: 2em;
  border: 2px solid #ccc;
  border-radius: 5px;
  width: 250px;
}


.formReview button {
  background-color: rgb(180, 218, 136);
  border: none;
  border-radius: 5px;
  padding: 10px;
  color: whitesmoke;
  font-weight: bold;
  cursor: pointer;
  margin-left: auto;
  margin-right: auto;
}

.addButton {
  position: relative;
  left: 45%;
  text-align: center;
  background-color: rgb(180, 218, 136);
  height: 50px;
  padding: 1em 2em;
  border-radius: 10px;
  transition: transform 0.3s ease;
  margin-bottom: 2em;
  border: none;
  cursor: pointer;
}

.addButton:hover {
  transform: scale(1.05);
}

.submitReviewButton {
  height: 48px;
  width: 75px;
}

.submitReviewButton:hover {
  background-color: rgb(111, 150, 66);
}

.loading-indicator {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(255, 255, 255, 0.8);
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  display: none;
}

.contentHeroFlex {
  margin: 10em 2em 5em 2em;
  display: flex;
  gap: 2em;
}

.imgFlex {
  width: 100%;
}

.contentHeroFlexText {
  font-size: 30px;
}

@media screen and (max-width: 1025px) {
  .contentHeroFlexText {
    font-size: 25px;
  }

  .grid-container {
    grid-template-columns: repeat(2, 1fr);
  }

  .nav-bar ul li a {
    font-size: smaller;
  }
}

@media screen and (max-width: 769px) {
  .contentHeroFlexText {
    font-size: 20px;
  }

  .addButton {
    left: 42%;
  }
}

@media screen and (max-width: 576px) {
  
  .contentHeroFlexText {
    font-size: 12px;
  }
  
  .togle {
    display: flex;
  }

  .nav-bar ul {
    position: absolute;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    height: 100vh;
    z-index: 2;
    width: 50%;
    transform: translate(-350px, 102px);
    transition: all 1s;
    border: 2px solid black;
    opacity: 1;
    background-color: #DAE2B6;
  }

.nav-bar ul.open {
  opacity: 1;
  transform: translate(-110px,102px);
}

.grid-container {
  grid-template-columns: 1fr;
}

.restaurant{
  padding: 0 1.5em;
}

.herotxt {
  padding: 5em 0em;
}


.herotxt h2 {
  font-size: 3em;
}

.kota {
  width: 10em;
  top: 1em;
  left: 0;
  font-size: smaller;
  background-color: #ffc293;
}


}

@media screen and (max-width: 428px) {
  .addButton {
    left: auto;
  }
  
  .togle {
    display: flex;
  }

.grid-container {
  grid-template-columns: 1fr;
}

.restaurant{
  width: 100%;
}

.rest-img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 2em;
  height: 15em; 
}


.herotxt {
  padding: 5em 0em;
}


.herotxt h2 {
  font-size: 3em;
}

.detailFlexContainer {
  flex-direction: column;
}

.detailBackground {
  max-width: 100%;
  margin-top: 1em;
}

.flexMenusItem {
  padding: 2em 2em;
  border: 2px solid black;
  font-size: 15px;
}

.menus {
  gap: 2em;
}


.formReview {
  width: 90%;
  font-size: small;
}

.addButton {
  text-align: center;
  background-color: rgb(180, 218, 136);
  margin-left: 35%;
  margin-right: auto;
  border-radius: 10px;
  transition: transform 0.3s ease;
  border: none;
  cursor: pointer;
}

}

@media screen and (max-width: 378px) {

  .favoriteListMinSreenSize {
    margin-left: 30px;
  }

  .contentHeroFlex {
    margin: 10em 2em 5em 2em;
    display: flex;
    flex-direction: column;
    gap: 2em;
  }
  

  .imgFlex {
    width: 100%;
  }

  .restaurant {
    width: 20em;
  }

  .formReview input {
  width: 200px;
}

.formReview textarea {
  width: 200px;
}


.rest-img {
  position: relative;
  display: block;
  width: 20em;
  height: 15em; 
  right: 1.5em;
}

.logo {
  font-size: smaller;
}


.herotxt {
  padding: 5em 0em;
}


.herotxt h2 {
  font-size: 3em;
}
}



@media screen and (max-width: 770px) {
  .grid-container {
    grid-template-columns: 1fr;
  }

  .nav-bar ul li a {
    font-size: 12.5px;
  }
}


@media screen and (max-width: 325px) {

  .rest-img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 20em;
    padding: 1.5em;
    border-radius: 2em;
  }

  .addButton {
    left: -15px;
  }

  body {
    font-size: smaller;
  }

  .flexMenusItem {
    width: 14em;
  }
  

  .menus {
    flex-direction: column;
  }

  .formReview input {
    width: 150px;
  }
  
  .formReview textarea {
    width: 150px;
  }
